@font-face {
    font-family: volkhov;
    src: url(../../static/Volkhov-Regular.ttf);
}

@font-face {
    font-family: poppine;
    src: url(../../static/PoppinsRegular.ttf);
}

.Typography>.font{
    font-family: poppine;
    color: #1565c0;
    cursor: pointer;
}

.Navbar{
    display: flex;
    position: fixed;
    top: 0;
    background-color: #fff;
    width: 100%;
    box-sizing: border-box;
    padding: 0.6rem 0.8rem;
    box-shadow: 0px 1px 2px -1px rgb(0 0 0 / 10%), 0px 1px 3px 0px rgb(0 0 0 / 7%), 0px 1px 8px 0px rgb(0 0 0 / 6%);
}